body {
    background-color: #F3E9D2;
    color: #092934;
    font-family: "open-sans", sans-serif;
    margin: 0;
  }
    
  
  .navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    top: 0;
    overflow: hidden;
    background-color: #88D498;
    display: block;
    box-shadow: 0 4px 8px 0 #A5C397;
  }
  
  a {
    float: left;
    display: block;
    color: #092934;
    text-align: center;
    padding: 20px 28px;
    text-decoration: none;
    cursor: pointer;
    font-family: open-sans, sans-serif;
    font-weight: 700;
    font-size: 18px;
    font-style: normal;
    
  }
  .logo {
    height: 50px;
    width: 50px;
    float: left;
    display:block;
    padding: 6px 15px;
  }
  
  a:last-child {
    float:right;
  }
  
  .inactive:hover {
    background-color: #A8F0B5;
    margin-top: -1px;
    
  }
  
  .active {
    background-color: #69C97E;
    border-bottom: 1px solid #092934;
  }